
let chai = require('chai');
let assert = chai.assert;
let eq = chai.assert.equal;

let Module = require('../index');

var Suite = describe('Module', function() {
  describe('module', function () {
    it('should have a module',  () => assert.ok(Module));
    it('is Bizarro', () => eq(0, 1, 'Or maybe its expected'));
  });
});



